<template>
  <div class="home" :style="styles">
    <header-table :index.sync="index"></header-table>
    <home v-show="index == 1"></home>
    <product v-show="index == 2"></product>
    <service v-show="index == 3"></service>
    <about-us v-show="index == 4"></about-us>
    <foot></foot>
  </div>
</template>

<script>
  import HeaderTable from '../components/HeaderTable.vue'
  import Foot from '../components/Foot.vue'

  import Home from '../tab/Home.vue'
  import Product from '../tab/Product.vue'
  import Service from '../tab/Service.vue'
  import AboutUs from '../tab/AboutUs.vue'


  export default {
    components: {
      HeaderTable,
      Foot,
      Home, Product, Service, AboutUs
    },
    data(){
      return {
        index: 0,
        styles: ''
      }
    },
    computed: {
    },
    methods: {
      getHeight(){
        var clientHeight = document.documentElement.clientHeight;
        this.height = `${clientHeight}px`;

        return {
          height: `${this.height}`
        }
      }
    },
    ready(){
      const vm = this;
      vm.$nextTick(function () {
        vm.styles = vm.getHeight();
        window.onresize = function () {
          vm.styles = vm.getHeight();
        };
      });
    },
    route: {
      data(transition){
        this.index = this.$route.query.index;
        if (!this.index) {
          this.index = 1;
        }

//        switch (transition.to.path) {
//          default:
//          case '/':
//            this.index = 0;
//            break;
//          case '/product':
//            this.index = 1;
//            break;
//          case '/service':
//            this.index = 2;
//            break;
//          case '/aboutus':
//            this.index = 3;
//            break;
//        }
        transition.next()
      },
      deactivate(transition){
        transition.next()
      }
    }

  }
</script>

<style>
  div.home {
    /*display: flex;*/
    /*overflow-x: hidden;*/
    overflow-y: scroll;
    flex-direction: column;
  }
</style>



