<template>
  <div class="header-table">
    <span>浙江贝丰橡胶有限公司</span>
    <div class="navigation">
      <div class="navigation-item" :class="{'navigation-item-selected' : index == 1}" @click="goToByIndex(1)">
        首页
      </div>
      <div class="navigation-item" :class="{'navigation-item-selected' : index == 2}" @click="goToByIndex(2)">
        产品中心
      </div>
      <div class="navigation-item" :class="{'navigation-item-selected' : index == 3}" @click="goToByIndex(3)">
        服务介绍
      </div>
      <div class="navigation-item" :class="{'navigation-item-selected' : index == 4}" @click="goToByIndex(4)">
        关于我们
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    props: ['index'],
    data(){
      return {}
    },
    methods: {
      goToByIndex(index){
        console.log('goToByIndex')
        this.index = index;
        this.$route.router.go({
          name : "main",
          query : {index : index}
        })
      }
    },
    components: {}
  }
</script>
<style scoped>
  .header-table {
    height: 100px;
    width: 90%;
    /*align-self: center;*/
    /*display: flex;*/
    margin: 0 5% ;
    background: #ffffff;
  }

  .header-table > span {
    font-family: 微软雅黑;
    font-size: 28px;
    font-weight: 900;
    color: #1564E9;
    height: 100px;
    line-height: 100px;
  }

  .header-table > div {
    display: flex;
    float: right;
    height: 100px;
    justify-content: center;
  }

  .navigation-item {
    height: 40px;
    margin-right: 40px;
    align-self: center;
    line-height: 40px;
    padding-left: 16px;
    padding-right: 16px;
    font-size: 18px;
    color: #333;
    /*flex: 1;*/
  }

  .navigation-item-selected, .navigation-item:hover {
    background: #1563e9 !important;
    color: #ffffff;
  }

</style>



